.about {
  &__intro {
    height: 710px;
    @include cover;
    background-image: url('../img/about/about-banner.png');
    @include center;
    @include r(767) {
      height: 500px; }
    h1 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.5;
      text-align: center;
      color: $white;
      margin-bottom: 40px;
      @include r(767) {
        font-size: 30px; } }
    p {
      font-size: 18px;
      font-weight: 300;
      line-height: 1.67;
      text-align: center;
      color: $pinkish-grey;
      @include r(767) {
        font-size: 16px; } } }
  &__started {
    padding: 100px 0 140px;
    @include r(767) {
      padding: 50px 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      color: $dark;
      text-align: center;
      padding-bottom: 20px;
      @include r(767) {
        font-size: 30px; } }
    p {
      margin-top: 30px;
      font-size: 18px;
      font-weight: 300;
      line-height: 1.67;
      text-align: center;
      color: $brownish-grey-two;
      @include r(767) {
        font-size: 14px;
        margin-top: 10px; } } }
  &__customers {
    background-color: $white-four;
    padding: 110px 0;
    @include r(767) {
      padding: 50px 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      color: $dark;
      text-align: center;
      margin-bottom: 90px;
      @include r(767) {
        font-size: 30px;
        margin-bottom: 40px; } }
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1px 3.5px 0 rgba(0, 0, 0, 0.16);
      background-color: $white;
      padding: 15px;
      @include center;
      height: 140px;
      margin-bottom: 15px;
      img {
        max-width: 100%; } }
    a.btn {
      color: $black;
      position: relative;
      left: 50%;
      transform: translate(-50%);
      margin-top: 50px; } }
  &__worth {
    padding: 115px 0;
    background-color: $dark-two;
    @include r(767) {
      padding: 50px 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      text-align: center;
      color: $white;
      margin-bottom: 85px;
      @include r(767) {
        font-size: 30px;
        margin-bottom: 40px; } }
    &__block {
      box-shadow: 0px 1px 3.5px 0 rgba(0, 0, 0, 0.16);
      border-radius: 10px;
      padding: 43px 45px;
      border: 1px solid $white-four;
      margin-bottom: 30px;
      width: calc(100% - 15px);
      margin-left: 7px;
      p {
        font-size: 18px;
        line-height: 1.11;
        text-align: center;
        color: $pinkish-grey; } } }
  &__facts {
    padding: 105px 0;
    background-color: $white-four;
    @include r(767) {
      padding: 50px 0 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      text-align: center;
      color: $dark;
      margin-bottom: 90px;
      @include r(767) {
        font-size: 30px;
        margin-bottom: 40px; } }
    &__block {
      @include center-column;
      @include r(1040) {
        margin-bottom: 60px; }
      img {
        margin-bottom: 24px; }
      strong {
        font-size: 18px;
        font-weight: bold;
        line-height: 1.11;
        text-align: center;
        color: $dark;
        margin-bottom: 5px; }
      span {
        display: block;
        font-size: 12px;
        font-weight: normal;
        color: $warm-grey;
        text-transform: uppercase; } } } }
