.intro {
  height: 795px;
  background: $dark-two;
  @include center;
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: rgba(#131c2e, 0.92);
    z-index: 2; }
  @include r(767) {
    height: 650px; }
  video {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    height: 100%;
    z-index: 1;
    @include r(767) {
      display: none; }
    @include rmin(1400) {
      width: 100%;
      height: auto;
      left: 0;
      top: 50%;
      transform: translate(0, -50%); } }

  .container {
    position: relative;
    z-index: 3; }
  strong {
    font-size: 16px;
    font-weight: bold;
    line-height: 1.56;
    color: $cerise;
    text-transform: uppercase;
    margin-bottom: 25px;
    display: block;
    @include r(767) {
      font-size: 12px; } }
  h2 {
    font-size: 30px;
    font-weight: 300;
    line-height: 1.67;
    letter-spacing: 1.2px;
    color: $white;
    margin-bottom: 75px;
    @include r(767) {
      font-size: 20px; } }
  &__btn {
    display: flex;
    justify-content: center;
    @include r(450) {
      flex-direction: column; }
    .btn-primary {
      margin-right: 10px;
      @include r(450) {
        margin: 0;
        margin-bottom: 20px; } } } }
