@font-face {
  font-family: "iconfont";
  src: url('../fonts/iconfont.eot');
  src: url('../fonts/iconfont.eot?#iefix') format('eot'),
    url('../fonts/iconfont.woff2') format('woff2'),
    url('../fonts/iconfont.woff') format('woff'),
    url('../fonts/iconfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

$icon-arrow-down: "\EA01";
$icon-arrow-right-2: "\EA02";
$icon-arrow-right: "\EA03";
$icon-check: "\EA04";
$icon-linkedin: "\EA05";


%icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  @extend %icon;
  display: inline-block;
}

.icon-arrow-down:before { content: $icon-arrow-down; }
.icon-arrow-right-2:before { content: $icon-arrow-right-2; }
.icon-arrow-right:before { content: $icon-arrow-right; }
.icon-check:before { content: $icon-check; }
.icon-linkedin:before { content: $icon-linkedin; }
