.baner {
  height: 795px;
  background-color: $white;
  @include center;
  @include r(767) {
    height: auto; }
  &__img {
    display: flex;
    justify-content: center; }
  &__text {
    @include r(767) {
      padding: 40px 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.15;
      color: $dark-two;
      padding-bottom: 50px;
      position: relative;
      @include r(767) {
        text-align: center; }
      &:before {
        content: '';
        position: absolute;
        bottom: 30px;
        width: 15px;
        height: 4.5px;
        border-radius: 2px;
        background-color: $dark-two;
        @include r(767) {
          left: 50%;
          transform: translate(-50%); } }
      span {
        font-size: 50px;
        font-weight: 300;
        color: $pink-red;
        display: block; } }
    p {
      font-size: 14px;
      line-height: 1.61;
      color: $dark-two;
      margin-bottom: 30px;
      @include r(767) {
        text-align: center; } }
    &__btn {
      @include center-y;
      @include r(767) {
        justify-content: center; }
      a {
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 600;
        line-height: 1;
        color: $dark-two;
        margin-right: 30px;
        &:hover {
          text-decoration: underline; }
        img {
          margin-right: 15px; } } } } }
