.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  padding: 20px 0;
  z-index: 999;
  transition: all .5s linear 0s;
}

.header.active {
  background: #ffffff;
  box-shadow: 5px 5px 15px 0 rgba(31, 31, 31, 0.07);
  top: -100px;
}

.header.active .header__nav ul li, .header.active .header__nav ul a {
  color: #131c2f;
}

.header.active .header__btn .btn-secondary {
  color: #131c2f;
}

.header.scroll {
  top: 0;
}

.header.app-function .header__nav ul li, .header.app-function .header__nav ul a {
  color: #131c2f;
}

.header.app-function .header__btn .btn-secondary {
  color: #131c2f;
}

.header .menu-open {
  position: absolute;
  left: 20px;
  top: 55%;
  width: 30px;
  height: 30px;
  display: none;
  z-index: 99;
  transform: translate(0, -50%) scale(0.8);
}

@media only screen and (max-width: 1040px) {
  .header .menu-open {
    display: block;
  }
}

.header .menu-open span {
  width: 30px;
  height: 2px;
  margin-top: 10px;
  background: #ec135c;
  display: block;
}

.header .menu-open span:before, .header .menu-open span:after {
  content: '';
  width: 30px;
  height: 2px;
  background: #ec135c;
  position: absolute;
  left: 0;
}

.header .menu-open span:before {
  top: 0;
}

.header .menu-open span:after {
  bottom: 8px;
}

.header__logo img {
  height: 40px;
}

.header__nav {
  display: flex;
  width: 100%;
  align-items: center;
}

.header__nav nav {
  flex: 1;
}

.header__nav ul {
  display: flex;
  justify-content: space-between;
}

.header__nav ul a, .header__nav ul li {
  font-size: 14px;
  font-weight: 300;
  color: #ffffff;
  position: relative;
}

.header__nav ul a i, .header__nav ul li i {
  font-size: 10px;
  position: relative;
  right: -10px;
  transition: all 0.3s;
}

.header__nav ul li.active i {
  transform: rotate(180deg);
}

.header__nav ul li:hover .header__nav__dropdown {
  opacity: 1;
  visibility: visible;
}

.header__nav__dropdown {
  width: 365px;
  position: absolute;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  background: #ffffff;
  right: -253px;
  top: 25px;
  border-radius: 10px;
  flex-direction: column;
  transition: all 0.3s;
  opacity: 0;
  visibility: hidden;
}

.header__nav__dropdown--icon {
  width: 445px;
  right: -308px;
}

.header__nav__dropdown:after {
  content: '';
  width: 100%;
  height: 10px;
  position: absolute;
  top: -10px;
}

.header__nav__dropdown:before {
  content: '';
  border: 8px solid transparent;
  border-bottom: 8px solid #ffffff;
  position: absolute;
  top: -16px;
  left: 30%;
}

.header__nav__dropdown__img {
  width: 75px;
}

.header__nav__dropdown__img img {
  height: 50px;
  margin-right: 30px;
}

.header__nav__dropdown__list {
  flex: 1 0;
}

.header__nav__dropdown li {
  cursor: pointer;
}

.header__nav__dropdown li:first-child a {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.header__nav__dropdown li:last-child a {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: none;
}

.header__nav__dropdown a {
  padding: 23px 33px;
  display: flex;
  border-bottom: 1px solid rgba(189, 189, 189, 0.5);
}

.header__nav__dropdown a:hover {
  background-color: #f5f5f5;
}

.header__nav__dropdown a b {
  font-size: 14px;
  font-weight: 600;
  color: #131c2f;
  margin-bottom: 10px;
  display: block;
}

.header__nav__dropdown a b span {
  font-size: 12px;
}

.header__nav__dropdown a p {
  font-size: 12px;
  line-height: 1.46;
  font-weight: normal;
  color: #616161;
  display: block;
}

.header__nav__dropdown a p span {
  color: #131c2e;
  display: block;
  font-weight: normal;
}

.header__btn {
  margin-left: 50px;
  display: flex;
  width: 265px;
  position: relative;
}

@media only screen and (max-width: 1024px) {
  .header__btn {
    width: 210px;
    margin-left: 20px;
  }
}

.header__btn .btn-primary {
  margin-right: 10px;
}

.header__btn .btn-secondary {
  cursor: pointer;
}

.header__btn .btn-secondary:hover .header__btn__login {
  opacity: 1;
  visibility: visible;
}

.header__btn .btn {
  font-weight: normal;
}

@media only screen and (max-width: 1024px) {
  .header__btn .btn {
    padding: 8px 10px;
  }
}

.header__btn__login {
  position: absolute;
  right: -10px;
  top: 50px;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  width: 150px;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  visibility: hidden;
  opacity: 0;
}

.header__btn__login:after {
  content: '';
  width: 100%;
  height: 20px;
  position: absolute;
  top: -18px;
  z-index: 10;
}

.header__btn__login:before {
  content: '';
  border: 8px solid transparent;
  border-bottom: 8px solid #ffffff;
  position: absolute;
  top: -16px;
  left: 54%;
}

.header__btn__login li {
  border-bottom: 1px solid #dedede;
  overflow: hidden;
}

.header__btn__login li:last-child {
  border-bottom: none;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.header__btn__login li:first-child {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.header__btn__login a {
  display: flex !important;
  align-items: center;
  padding: 16px 18px !important;
  color: #000 !important;
  font-size: 12px !important;
  font-weight: 600 !important;
}

.header__btn__login a:hover {
  background: #f5f5f5;
}

.header__btn__login a img {
  width: 16px;
  margin-right: 10px;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: 300;
  box-sizing: border-box;
  position: relative;
}

.container {
  width: 100% !important;
  max-width: 1250px;
  box-sizing: border-box;
}

@media only screen and (max-width: 1270px) {
  .container {
    padding: 0 15px;
  }
}

a {
  text-decoration: none;
  transition: all 0.3s;
}

input[type="submit"] {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
}

.flex {
  display: flex;
}

@media only screen and (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1040px) {
  .hidden-sm {
    display: none !important;
  }
}

@media only screen and (min-width: 1040px) {
  .hidden-md {
    display: none !important;
  }
}

.btn {
  font-size: 12px;
  font-weight: bold;
  line-height: 1 !important;
  color: #ffffff;
  padding: 11px 24px;
  text-transform: uppercase;
  display: inline-block;
  border-radius: 20.5px;
}

.btn-primary {
  background-color: #ec135c;
  border: 1px solid #ec135c;
}

.btn-primary:hover {
  background: none;
  color: #ec135c;
}

.btn-secondary {
  border: solid 1px #bdbdbd;
}

.btn-secondary:hover {
  background: #ec135c;
  color: #ffffff !important;
  border: 1px solid #ec135c;
}

.fl {
  visibility: visible;
  animation-duration: 1.5s;
  animation-name: fl;
}

@keyframes fl {
  0% {
    transform: rotate3d(0, 0, 90deg);
  }
  50% {
    transform: rotate3d(0, 0, 30deg);
  }
  100% {
    transform: rotate3d(0, 0, 0deg);
  }
}

