}

.footer__nav__block h3 {
  margin-bottom: 30px;
  font-size: 14px;
  font-weight: 600;
  line-height: 2.5;
  color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .footer__nav__block h3 {
    margin-bottom: 20px;
    display: block;
    width: calc(100% + 30px);
    border-bottom: 1px solid #ffffff;
    position: relative;
    margin-bottom: 0;
    left: 50%;
    padding: 10px 10px;
    transform: translate(-50%);
  }
}

.footer__nav__block h3 i {
  display: none;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translate(0, -50%);
  font-weight: 300;
  color: #ffffff;
  transition: all 0.3s;
}

@media only screen and (max-width: 767px) {
  .footer__nav__block h3 i {
    display: block;
  }
}

.footer__nav__block h3.active i {
  transform: translate(0, -50%) rotate(180deg);
  color: #ec135c;
}

@media only screen and (max-width: 767px) {
  .footer__nav__block:first-child h3 {
    border-top: 1px solid #ffffff;
  }
}

@media only screen and (max-width: 767px) {
  .footer__nav__block ul {
    display: none;
    width: calc(100% + 30px);
    border-bottom: 1px solid #ffffff;
    left: 50%;
    position: relative;
    padding: 20px 10px 10px;
    padding-left: 30px;
    transform: translate(-50%);
  }
}

.footer__nav__block li {
  margin-bottom: 15px;
}

.footer__nav__block li a {
  font-size: 14px;
  color: #bdbdbd;
  font-weight: 300;
}

.footer__nav__block li a:hover {
  text-decoration: underline;
}

.footer__copyright {
  margin-top: 40px;
}

.footer__copyright p {
  font-size: 12px;
  color: #5f626b;
  line-height: 1.4;
}

.footer__policy {
  margin-top: 40px;
}

@media only screen and (max-width: 767px) {
  .footer__policy {
    margin-top: 20px;
  }
}

.footer__policy a, .footer__policy span {
  font-size: 12px;
  color: #5f626b;
  transition: all 0.3s;
}

.footer__policy span {
  margin: 0 5px;
}

.footer__policy a:hover {
  text-decoration: underline;
}

.blocker {
