.pop-up .close-modal {
  display: none !important;
}

.pop-up__close {
  width: 30px;
  height: 30px;
  position: absolute;
  right: 20px;
  top: 20px;
}

.pop-up__close span:before, .pop-up__close span:after {
  content: '';
  width: 15px;
  height: 2px;
  background: #bdbdbd;
  position: absolute;
  top: 15px;
  right: 8px;
  transition: all 0.3s;
}

.pop-up__close span:before {
  transform: rotate(45deg);
}

.pop-up__close span:after {
  transform: rotate(-45deg);
}

.pop-up__close:hover span:before, .pop-up__close:hover span:after {
  background: #f2015b;
}

