.menu {
  position: fixed;
  left: -100%;
  top: 0;
  width: 255px;
  height: 100vh;
  background: #ffffff;
  z-index: 999;
  transition: all 0.3s;
  overflow-y: scroll;
}

.menu.active {
  left: 0;
}

.menu-bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
  display: none;
}

.menu li {
  border-bottom: 1px solid #eee;
  position: relative;
}

.menu li a {
  padding: 11px 20px;
  color: #5e5a54;
  font-size: 16px;
  font-weight: normal;
  display: block;
}

.menu li a.menu__item.active {
