}

.menu li a.menu__item.active span {
  background: #da0e2b;
}

.menu li a.menu__item.active span i {
  color: #ffffff;
  transform: rotate(180deg);
}

.menu li .menu__item span {
  position: absolute;
  top: 0;
  right: 0;
  width: 38px;
  height: 38px;
  background-color: rgba(238, 238, 238, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}

.menu li .menu__item i {
  color: #5e5a54;
  font-size: 13px;
  font-weight: 300;
  transition: all 0.3s;
}

.menu__dropdown {
  background-color: #eee;
  padding-left: 15px;
  display: none;
}

.menu__dropdown b {
  font-weight: normal;
}

.menu__dropdown a {
  display: flex !important;
  align-items: center;
}

.menu__dropdown__img {
  width: 70px;
}

.menu__dropdown__list {
  flex: 1;
