.intro {
  height: 795px;
  background: #131c2e;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.intro:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: rgba(19, 28, 46, 0.92);
  z-index: 2;
}

@media only screen and (max-width: 767px) {
  .intro {
    height: 650px;
  }
}

.intro video {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  height: 100%;
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .intro video {
    display: none;
  }
}

@media only screen and (min-width: 1400px) {
  .intro video {
    width: 100%;
    height: auto;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
}

.intro .container {
  position: relative;
  z-index: 3;
}

.intro strong {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.56;
  color: #ec135c;
  text-transform: uppercase;
  margin-bottom: 25px;
  display: block;
}

@media only screen and (max-width: 767px) {
  .intro strong {
    font-size: 12px;
  }
}

.intro h2 {
  font-size: 30px;
  font-weight: 300;
  line-height: 1.67;
  letter-spacing: 1.2px;
  color: #ffffff;
  margin-bottom: 75px;
}

@media only screen and (max-width: 767px) {
  .intro h2 {
    font-size: 20px;
  }
}

.intro__btn {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 450px) {
  .intro__btn {
    flex-direction: column;
  }
}

.intro__btn .btn-primary {
  margin-right: 10px;
}

@media only screen and (max-width: 450px) {
  .intro__btn .btn-primary {
    margin: 0;
    margin-bottom: 20px;
  }
}

.baner {
  height: 795px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .baner {
    height: auto;
  }
}

.baner__img {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  .baner__text {
    padding: 40px 0;
  }
}

.baner__text h2 {
  font-size: 40px;
  font-weight: 300;
  line-height: 1.15;
  color: #131c2e;
  padding-bottom: 50px;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .baner__text h2 {
    text-align: center;
  }
}

.baner__text h2:before {
  content: '';
  position: absolute;
  bottom: 30px;
  width: 15px;
  height: 4.5px;
  border-radius: 2px;
  background-color: #131c2e;
}

@media only screen and (max-width: 767px) {
  .baner__text h2:before {
    left: 50%;
    transform: translate(-50%);
