.reviews {
  background: rgb(242, 242, 242);
  padding: 135px 0;
  @include r(767) {
    padding: 60px 0; }
  .swiper {
    position: relative; }
  &__title {
    h2 {
      font-size: 40px;
      font-weight: 300;
      color: $dark-three;
      text-align: center;
      margin-bottom: 30px;
      @include r(767) {
        font-size: 30px; } }
    p {
      font-size: 16px;
      font-weight: 300;
      line-height: 1.72;
      text-align: center;
      color: $dark-two;
      margin-bottom: 120px;
      @include r(767) {
        font-size: 14px; } } }
  &__slide {
    &__logo {
      width: 125px;
      height: 125px;
      margin-top: 8px;
      @include center;
      position: relative;
      left: 50%;
      transform: translate(-50%);
      &--bg {
        border-radius: 100%; }
      img {
        width: 90px; } }
    &__text {
      p {
        font-size: 14px;
        line-height: 1.79;
        text-align: center;
        color: $brownish-grey-two;
        margin-top: 35px;
        margin-bottom: 55px; } }
    &__img {
      width: 80px;
      height: 80px;
      opacity: 0.5;
      box-shadow: 0px 1.5px 20px 0 rgba(0, 0, 0, 0.06);
      background-color: $white;
      @include center;
      border-radius: 100%;
      transform: scale(0.8);
      overflow: hidden;
      margin: 0 auto;
      @include r(550) {
        width: 50px;
        height: 50px; }
      img {
        width: 80%; }
      &-primary {
        img {
          width: 60%; } } }
    &__name {
      width: 210px;
      position: relative;
      left: 50%;
      transform: translate(-50%);
      margin-top: 20px;
      opacity: 0;
      @include tr;
      b, p {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.43;
        text-align: center;
        color: $dark-two; }
      p {
        font-weight: 300;
        font-size: 12px;
        color: $warm-grey; } } }
  .swiper-button-next, .swiper-button-prev {
    background: none;
    img {
      width: 40px; } }
  .swiper-button-next {
    right: -130px;
    @include r(1040) {
      right: -50px; }
    @include r(767) {
      top: 10%;
      right: 10px; } }
  .swiper-button-prev {
    left: -150px;
    @include r(1040) {
      left: -50px; }
    @include r(767) {
      top: 10%;
      left: 0; } }
  .swiper-slide-thumb-active {
    .reviews__slide__img {
      opacity: 1; } }
  .gallery-thumbs {
    .swiper-wrapper {
      @include center;
      align-items: flex-start; }
    .swiper-slide-thumb-active {
      .reviews__slide__img {
        transform: scale(1); }
      .reviews__slide__name {
        opacity: 1; } } } }
