.mobile_tour {
  background-image: url('../img/line_bg.png');
  background-size: 8px 8px;
  &__intro {
    height: 705px;
    background: $dark;
    @include center;
    @include r(767) {
      height: 930px; }
    &__img {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      @include r(767) {
        justify-content: center;
        margin-top: 40px; }
      img {
        max-width: 100%; } }
    h1 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.25;
      color: $white;
      margin-bottom: 35px;
      @include r(767) {
        font-size: 30px;
        margin-top: 100px;
        text-align: center; } }
    p {
      font-size: 14px;
      line-height: 1.61;
      color: $white-60;
      padding-right: 100px;
      margin-bottom: 40px;
      @include r(767) {
        padding-right: 0;
        text-align: center; } }
    a {
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 1.61;
      color: $white;
      font-weight: 600;
      @include r(767) {
        justify-content: center; }
      &:hover {
        text-decoration: underline; }
      img {
        margin-right: 15px; } } }
  &__experience {
    padding: 100px 0;
    background: rgb(242, 242, 242);
    @include r(767) {
      padding: 50px 0 0; }
    h2 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.25;
      text-align: center;
      color: $dark-two;
      margin-bottom: 20px;
      @include r(767) {
        font-size: 28px; } }
    p {
      font-size: 16px;
      font-weight: 300;
      line-height: 1.5;
      text-align: center;
      color: $dark-two; }
    &__content {
      margin-top: 95px;
      background: $white;
      border-radius: 10px;
      display: flex;
      overflow: hidden;
      box-shadow: 0px 1.5px 20px 0 rgba(0, 0, 0, 0.06);
      background-color: $white;
      margin-bottom: 50px;
      @include r(767) {
        flex-direction: column;
        box-shadow: none;
        background: none;
        overflow: visible;
        margin-top: 40px; } }
    &__block {
      width: 100%;
      box-shadow: 0px 1.5px 20px 0 rgba(0, 0, 0, 0.06);
      background-color: $white;
      @include center-column;
      justify-content: flex-start;
      padding: 25px;
      padding-bottom: 55px;
      @include r(767) {
        margin-bottom: 20px;
        border-radius: 10px; }
      &__img {
        overflow: hidden;
        border-radius: 10px;
        margin-bottom: 30px;
        img {
          width: 100%; } }
      &__text {
        max-width: 300px;
        strong {
          font-size: 18px;
          font-weight: 600;
          line-height: 1.39;
          text-align: center;
          display: inline-block;
          color: $dark-two;
          margin-bottom: 25px; }
        p {
          font-size: 14px;
          line-height: 1.79;
          text-align: center;
          color: $dark-two-80; } } } }
  &__capability {
    padding: 110px 0 140px;
    @include r(767) {
      padding: 90px 0 40px; }
    &__title {
      margin-bottom: 120px;
      h2 {
        font-size: 40px;
        font-weight: 300;
        line-height: 1.25;
        text-align: center;
        color: $dark-two;
        margin-bottom: 20px;
        @include r(767) {
          font-size: 30px; } }
      p {
        font-size: 16px;
        font-weight: 300;
        line-height: 1.5;
        text-align: center;
        color: $dark-two; } }
    &__block {
      margin-bottom: 80px;
      @include r(767) {
        margin-bottom: 100px; }
      h3 {
        font-size: 30px;
        font-weight: 300;
        line-height: 1.33;
        color: $dark-two;
        margin-bottom: 30px; }
      p {
        font-size: 14px;
        line-height: 1.79;
        color: $brownish-grey-two;
        margin-bottom: 30px; }
      &__img {
        margin: 0 90px;
        position: relative;
        top: -70px;
        @include r(767) {
          margin: 0;
          top: 20px; }
        img {
          box-shadow: 0px 1.5px 40px 10px rgba(0, 0, 0, 0.1); } } } }
  &__advantages {
    padding: 125px 0 40px;
    background-image: url('../img/line_bg.png');
    background-size: 8px 8px;
    @include r(767) {
      padding: 50px 0; }
    &__title {
      margin-bottom: 120px;
      @include r(767) {
        margin-bottom: 50px; }
      h2 {
        font-size: 40px;
        font-weight: 300;
        line-height: 1.25;
        text-align: center;
        color: $dark-two;
        margin-bottom: 20px;
        @include r(767) {
          font-size: 30px; } }
      p {
        font-size: 16px;
        font-weight: 300;
        text-align: center;
        line-height: 1.5;
        color: $dark-two; } }
    &__block {
      @include center;
      position: relative;
      padding: 0 30px;
      padding-bottom: 40px;
      height: 150px;
      margin-bottom: 40px;
      @include r(767) {
        display: block;
        height: auto; }
      &:after {
        content: '';
        width: 1px;
        height: 110px;
        background: #cecece;
        top: 0px;
        right: 0px;
        position: absolute;
        z-index: 10;
        @include r(767) {
          display: none; } }
      &:before {
        content: '';
        width: 83%;
        height: 1px;
        background: #cecece;
        position: absolute;
        bottom: 0;
        left: 6%;
        @include r(767) {
          display: none; } }
      &--border {
        &:before {
          display: none; } }
      &--right {
        &-1 {
          &:after {
            @include r(1024) {
              display: none !important; } } }
        &:after {
          display: none;
          @include r(1024) {
            display: block; } } }
      &--mobile {
        &:after {
          @include r(1024) {
            display: none; } }
        &:before {
          @include r(1024) {
            display: block; } } }
      &__img {
        width: 100px;
        height: 100px;
        box-shadow: 0px 1.5px 20px 0 rgba(0, 0, 0, 0.06);
        background-color: $white;
        @include center;
        border-radius: 100%;
        margin-right: 30px;
        @include r(1250) {
          width: 80px;
          height: 80px; }
        @include r(767) {
          position: relative;
          left: 50%;
          transform: translate(-50%);
          margin-bottom: 10px; } }
      &__text {
        flex: 1;
        position: relative;
        padding-right: 5px;
        strong {
          font-size: 18px;
          font-weight: 600;
          color: $dark-three;
          margin-bottom: 15px;
          display: block;
          @include r(767) {
            text-align: center; } }
        p {
          font-size: 14px;
          line-height: 1.43;
          color: $brownish-grey-two;
          max-width: 230px;
          @include r(767) {
            text-align: center;
            max-width: 100%; } } } } }
  &__relations {
    height: 400px;
    @include center;
    background: rgb(242, 242, 242);
    h2 {
      font-size: 40px;
      font-weight: 300;
      text-align: center;
      line-height: 1.3;
      color: $dark-two;
      margin-bottom: 20px;
      @include r(767) {
        font-size: 28px; } }
    p {
      font-size: 14px;
      font-weight: 300;
      line-height: 1.73;
      text-align: center;
      color: $dark-two; }
    a {
      margin-top: 45px; } } }
