.info {
  background-color: $dark-three;
  padding: 70px 0;
  h2 {
    font-size: 40px;
    font-weight: 300;
    text-align: center;
    line-height: 1.3;
    color: $white;
    margin-bottom: 15px;
    @include r(767) {
      font-size: 25px; } }
  p {
    font-size: 14px;
    font-weight: 300;
    line-height: 1.53;
    text-align: center;
    color: $white-60;
    max-width: 508px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    margin-bottom: 30px; }
  .btn {
    position: relative;
    left: 50%;
    transform: translate(-50%); } }
