.app-function {
  &__intro {
    height: 705px;
    padding-top: 100px;
    background-color: $white-six;
    @include r(767) {
      height: 630px; }
    .container {
      position: relative;
      height: 100%; }
    .row {
      width: 100%; }
    &__content {
      @include center-y;
      height: 100%;
      @include r(600) {
        align-items: flex-start;
        justify-content: center; } }
    img {
      position: absolute;
      right: 26%;
      bottom: 0;
      transform: translate(20%);
      @include r(1024) {
        right: 14%;
        transform: none; }
      @include r(910) {
        right: 0; }
      @include r(780) {
        width: 45%; }
      // +r(600)
 }      //   width: 100%
    &__block {
      border-radius: 10px;
      box-shadow: 0px 3px 0 0 rgba(0, 0, 0, 0.12);
      background-color: $white;
      padding: 60px 75px;
      padding-right: 230px;
      max-width: 605px;
      width: 100%;
      @include r(767) {
        padding: 20px 15px;
        width: 65%; }
      @include r(600) {
        width: 100% !important; }
      strong {
        font-size: 16px;
        font-weight: bold;
        line-height: 1.56;
        color: $dark;
        text-transform: uppercase;
        position: relative;
        margin-bottom: 55px;
        display: block;
        @include r(767) {
          text-align: center; }
        &:before {
          content: '';
          position: absolute;
          left: 0;
          bottom: -15px;
          width: 28px;
          height: 5px;
          border-radius: 2.5px;
          background-color: $cerise;
          @include r(767) {
            left: 50%;
            transform: translate(-50%); } } }
      h1 {
        font-size: 40px;
        font-weight: 300;
        line-height: 1.13;
        color: $dark-three;
        margin-bottom: 28px;
        @include r(767) {
          font-size: 26px;
          text-align: center; } }
      p {
        font-size: 14px;
        line-height: 1.61;
        color: $warm-grey;
        @include r(767) {
          text-align: center; } } } }
  &__description {
    padding: 120px 0;
    @include r(767) {
      padding: 50px 0; }
    p {
      font-size: 20px;
      font-weight: 300;
      line-height: 2;
      text-align: center;
      color: $dark-two;
      max-width: 1020px;
      width: 100%;
      left: 50%;
      position: relative;
      transform: translate(-50%);
      display: block;
      @include r(767) {
        font-size: 14px; } } }
  &__examples {
    h2 {
      font-size: 50px;
      font-weight: 300;
      text-align: center;
      color: $dark-two;
      margin: 40px 0 75px;
      @include r(767) {
        font-size: 40px; } }
    &__content {
      background: $white-three;
      padding: 70px 0; }
    &__block {
      padding: 50px 0;
      &__img {
        position: relative;
        top: -70px;
        @include r(767) {
          top: 0; }
        img {
          width: 100%; } }
      h3 {
        font-size: 30px;
        font-weight: 300;
        line-height: 1.33;
        color: $dark-two;
        margin-bottom: 40px;
        @include r(767) {
          font-size: 25px;
          text-align: center; } }
      p, li {
        font-size: 14px;
        font-weight: 300;
        line-height: 1.96;
        color: $dark-two;
        margin-bottom: 40px; }
      p {
        @include r(767) {
          text-align: center; } }
      li {
        position: relative;
        padding-left: 40px;
        max-width: 390px;
        img {
          position: absolute;
          left: 0;
          top: 7px; } } } }
  &__start {
    height: 555px;
    @include center;
    @include r(767) {
      height: 300px; }
    h2 {
      max-width: 620px;
      width: 100%;
      position: relative;
      left: 50%;
      transform: translate(-50%);
      font-size: 40px;
      font-weight: 300;
      text-align: center;
      color: $dark-two;
      margin-bottom: 60px;
      line-height: 1.5;
      @include r(767) {
        font-size: 22px;
        margin-bottom: 30px; } }
    .btn {
      position: relative;
      left: 50%;
      transform: translate(-50%); } }




  &.expansion {
    .app-function__intro {
      @include r(900) {
        height: 740px; }
      @include r(600) {
        height: 650px; }
      img {
        right: 18%;
        @include r(1150) {
          right: 0;
          transform: none; }
        @include r(1000) {
          width: 52%; }
        @include r(600) {
          width: 320px; } } } }
  &.retention {
    .app-function__intro__block {
      max-width: 715px;
      margin-bottom: 50px; }
    .app-function__intro img {
      right: 18%;
      @include r(1024) {
        right: 15%;
        transform: none; }
      @include r(900) {
        right: 0; } }
    .app-function__intro__block h1 {
      @include r(767) {
        max-width: 350px; } } }
  &.support {
    .app-function__intro {
      img {
        @include r(1250) {
          right: 10%;
          transform: none; }
        @include r(950) {
          right: 0;
          width: 37%; } } } }
  &.advocacy {
    .app-function__intro {
      @include r(767) {
        height: 500px; } }
    .app-function__intro img {
      right: 18%;
      bottom: 25px;
      @include r(1250) {
        transform: none;
        right: 0;
        bottom: 0;
        width: 40%; }
      @include r(767) {
        display: none; } }
    .app-function__intro__block {
      padding: 60px 75px;
      padding-right: 165px;
      margin-bottom: 40px;
      @include r(767) {
        padding: 10px 20px;
        width: 100%;
        max-width: 100%; } } } }
