.team {
  background: $white;
  &__title {
    background-color: $white-four;
    height: 400px;
    @include center;
    h1 {
      font-size: 40px;
      font-weight: 300;
      color: $dark-two;
      @include center-column;
      margin-bottom: 25px;
      @include r(767) {
        font-size: 33px; }
      strong {
        font-size: 14px;
        font-weight: bold;
        color: $cerise;
        text-transform: uppercase; } }
    p {
      font-size: 14px;
      font-weight: 300;
      line-height: 1.79;
      text-align: center;
      color: $dark-two; } }
  &__content {
    padding-bottom: 280px;
    @include r(767) {
      padding-bottom: 50px; }
    .col-xs-12:nth-child(n+13) {
      display: none; } }
  &__block {
    background-image: linear-gradient(to top, $white-six, #f9f9f9, $white);
    @include center-column;
    padding: 0 10px;
    padding-bottom: 25px;
    border-radius: 15px;
    margin-top: 50px;
    position: relative;
    height: calc(100% - 50px);
    min-height: 320px;
    @include r(767) {
      width: 60%;
      position: relative;
      left: 50%;
      transform: translate(-50%); }
    @include r(500) {
      width: 100%; }
    &__hover {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
      background-color: $dark-three;
      @include center-column;
      padding: 0 50px;
      transition: all 0.5s;
      p {
        font-size: 20px;
        font-weight: 300;
        line-height: 1.5;
        text-align: center;
        color: $white;
        margin-bottom: 35px; } }
    &__img {
      width: 172px;
      height: 172px;
      overflow: hidden;
      border-radius: 100%;
      background-color: #e3e7f0;
      position: relative;
      margin-bottom: 40px;
      img {
        height: 150px;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%); } }
    &__info {
      @include center-column;
      strong {
        font-size: 14px;
        font-weight: bold;
        line-height: 1.79;
        color: $dark-two;
        text-transform: uppercase;
        text-align: center; }
      p {
        font-size: 12px;
        color: $brownish-grey-two;
        text-align: center;
        min-height: 12px; }
      i {
        color: rgb(20, 88, 144);
        margin-top: 20px; } } } }
