.pricing {
  padding-top: 160px;
  background-color: $white-four;
  h1 {
    font-size: 50px;
    font-weight: 300;
    color: $dark-two;
    text-align: center;
    margin-bottom: 60px;
    @include r(767) {
      font-size: 35px; } }
  .tabs {
    @include center-column;
    &__caption {
      display: flex;
      justify-content: center;
      margin-bottom: 60px;
      li {
        cursor: pointer;
        border-radius: 17.5px;
        border: solid 1px transparent;
        padding: 13px 20px;
        font-size: 12px;
        line-height: 1;
        font-weight: bold;
        color: $dark;
        text-transform: uppercase;
        @include tr;
        @include r(550) {
          font-size: 11px;
          padding: 13px 7px; }
        &:hover {
          color: $pink-red; }
        &.active {
          border: solid 1px $dark-two;
          &:hover {
            color: $dark;
            cursor: default; } } } }
    &__content {
      display: none;
      width: 100%;
      &.active {
        display: block; } } }
  &__tariff {
    margin-bottom: 60px;
    &__block {
      border-radius: 10px;
      background-color: $white;
      padding: 60px 55px;
      height: 100%;
      overflow: hidden;
      position: relative;
      @include r(1200) {
        padding: 60px 35px; }
      @include r(1024) {
        padding: 60px 10px; }
      @include r(767) {
        padding: 50px 40px 90px;
        margin-bottom: 30px;
        height: auto; }
      &__mobile {
        @include r(767) {
          display: none; }
        &-btn {
          width: 100%;
          @include center-column;
          font-size: 14px;
          font-weight: 600;
          line-height: 1.43;
          text-align: center;
          color: $cerise;
          padding-bottom: 20px;
          padding-top: 20px;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          &.active {
            i {
              transform: rotate(180deg); } } } }
      &__popular {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background-color: $dark-two;
        padding: 7px;
        p {
          font-size: 12px;
          font-weight: bold;
          text-align: center;
          color: $white; } }
      &__title {
        h2 {
          font-size: 30px;
          font-weight: 300;
          color: $dark-two;
          margin-bottom: 30px;
          text-align: center; }
        p {
          font-size: 14px;
          font-weight: 300;
          line-height: 1.43;
          text-align: center;
          color: $dark;
          margin-bottom: 23px; } }
      &__coins {
        @include center-column;
        p {
          height: 15px;
          display: block;
          font-size: 12px;
          font-weight: 300;
          text-align: center;
          color: $dark;
          text-transform: uppercase;
          margin-bottom: 10px; }
        strong {
          font-size: 30px;
          font-weight: 600;
          color: $dark-two;
          display: flex;
          align-items: flex-end;
          span {
            font-size: 14px;
            font-weight: 300;
            position: relative;
            top: -14px; }
          b {
            font-size: 14px;
            position: relative;
            top: -2px; }
          i {
            font-size: 14px;
            margin-left: 7px;
            color: $dark-two;
            font-weight: normal;
            font-style: normal;
            position: relative;
            top: -2px; } } }
      &__details {
        @include center-column;
        min-height: 115px;
        @include r(767) {
          min-height: auto; }
        a {
          font-size: 12px;
          font-weight: 300;
          color: $dark;
          margin-bottom: 15px;
          text-decoration: underline;
          margin-top: 25px; }
        span {
          display: block;
          font-size: 12px;
          color: rgba(19, 28, 47, 0.6);
          margin-bottom: 50px;
          display: block;
          @include r(767) {
            margin-bottom: 0px; } } }
      &__btn {
        @include r(767) {
          margin-top: 30px; }
        a {
          width: 100%;
          color: $black;
          @include center; } }
      &__includes {
        padding: 60px 0 20px;
        min-height: 125px;
        h3 {
          font-size: 14px;
          font-weight: 600;
          text-align: center;
          color: $dark-two; }
        p {
          font-size: 12px;
          text-align: center;
          color: $dark-two;
          margin-top: 20px; } }
      &__all {
        h3 {
          text-align: center;
          margin-bottom: 20px;
          a {
            font-size: 14px;
            font-weight: 600;
            color: $dark-two;
            text-decoration: underline; } }
        ul {
          @include center-column; }
        li {
          font-size: 12px;
          text-align: center;
          color: $dark-two;
          margin-bottom: 10px; } } }
    &__info {
      margin-top: 50px;
      p {
        font-size: 12px;
        font-weight: 300;
        line-height: 2.08;
        text-align: center;
        color: $dark-two;
        &:first-child {
          font-size: 14px; } } } }
  &__questions {
    padding: 60px 0 140px;
    h2 {
      font-size: 30px;
      font-weight: 300;
      color: $dark-two;
      line-height: 1.3;
      text-align: center;
      margin-bottom: 90px; }
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1.5px 5px 0 rgba(0, 0, 0, 0.11);
      border: solid 0 #000000;
      background-color: $white;
      padding: 35px;
      margin-bottom: 20px;
      display: inline-block;
      width: 100%;
      @include r(767) {
        padding: 25px 20px; }
      &__text {
        margin-top: 20px; }
      strong {
        font-size: 14px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: 0.1px;
        text-align: left;
        color: $dark-three;
        position: relative;
        display: block;
        padding-right: 25px;
        cursor: pointer;
        @include tr;
        &:hover {
          color: $pink-red; }
        &.active {
          i {
            transform: rotate(0deg); } }
        i {
          position: absolute;
          right: 0;
          top: 5px;
          transform: rotate(180deg);
          @include tr; } }
      div, li, b, a {
        font-size: 12px;
        font-weight: 300;
        line-height: 2.08;
        color: $dark;
        margin-bottom: 40px;
        @include r(767) {
          text-align: center; }
        &:last-child {
          margin-bottom: 0; } }
      a {
        &:hover {
          text-decoration: underline; } }
      ul {
        margin-bottom: 40px; }
      li, b {
        margin-bottom: 0; }
      b {
        font-weight: 600; } } } }
