.simplicity {
  padding: 100px 0;
  background-color: $dark;
  @include r(767) {
    padding-bottom: 0; }
  &__title {
    h2 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.5;
      text-align: center;
      color: $white-three;
      margin-bottom: 40px;
      @include r(767) {
        font-size: 25px; } }
    p {
      font-size: 16px;
      font-weight: 300;
      line-height: 1.72;
      text-align: center;
      color: $white;
      margin-bottom: 100px;
      @include r(767) {
        font-size: 14px; } } }
  &__block {
    @include center-column;
    position: relative;
    @include r(767) {
      margin-bottom: 80px; }
    &--last {
      .simplicity__block__img {
        border: none;
        &:before, &:after {
          display: none; } } }
    h2 {
      font-size: 12px;
      line-height: 1.25;
      text-align: center;
      color: $white-80;
      position: relative;
      display: inline-block;
      max-width: 160px;
      margin-bottom: 50px;
      @include r(767) {
        margin-bottom: 20px; }
      &:before {
        content: '';
        position: absolute;
        bottom: -15px;
        width: 40%;
        height: 4px;
        border-radius: 1.7px;
        background-color: $pink-red;
        left: 50%;
        transform: translate(-50%); } }
    &__img {
      height: 320px;
      @include center;
      position: relative;
      width: 100%;
      border-right: 1px solid rgba($white, 0.3);
      padding-top: 20px;
      &--last {
        img {
          width: 180px; } }
      @include r(767) {
        height: auto;
        border-right: none;
        border-bottom: 1px solid rgba($white, 0.3);
        padding-bottom: 40px; }
      &:before, &:after {
        content: '';
        position: absolute;
        right: -100px;
        border: 60px solid transparent;
        border-left: 40px solid rgba($white, 0.3);
        @include r(767) {
          border: 60px solid transparent;
          border-top: 40px solid rgba($white, 0.3);
          right: auto;
          bottom: -100px; } }
      &:after {
        border-left: 40px solid $dark;
        right: -99px;
        @include r(767) {
          border: 60px solid transparent;
          border-top: 40px solid $dark;
          right: auto;
          bottom: -99px; } }
      img {
        @include r(1150) {
          width: 70%; }
        @include r(767) {
          width: auto;
          max-width: 100%; } }
      p {
        @include center-column;
        font-size: 12px;
        font-weight: bold;
        color: $blue-grey;
        text-transform: uppercase;
        width: 110px;
        text-align: center;
        border-right: 1px solid rgba($white, 0.3);
        margin-right: 30px;
        @include r(1024) {
          font-size: 10px; }
        @include r(767) {
          width: 140px;
          margin-right: 0; }
        &:last-child {
          border: none;
          margin: 0; }
        img {
          margin-bottom: 20px;
          width: 35px; } } } } }
